import './styles/Letter.css';
import letters from './letters-data.json';



function Letter({inputValue}) {
  return (<div> {letters.filter(letter => letter.id===inputValue).map((thisLetter) => (
    <div key={thisLetter.id} className="letter">
      <div className="letter-section">
        <div className="bubble-text">
          {thisLetter.S1.map((p, index) => (<p className="p" key={'P1'+index}>{p}</p>))}
          <img src={"./assets/"+thisLetter.logo} alt="logo" id="companyLogo"/>
        </div>
        <a href="#LS2" className="scroll">SCROLL RIGHT &#8605;</a>
      </div>
      <div id="LS2" className="letter-section s2">
        <div id="skills">
          <i className="fab fa-html5"></i>
          <i className="fab fa-css3-alt"></i>
          <i className="fab fa-sass"></i>
          <i className="fab fa-js-square"></i>
          <i className="fab fa-react"></i>
          <i className="fab fa-git-alt"></i>
          <i className="fab fa-github"></i>
          <i className="fas fa-universal-access"></i>
          <i className="fab fa-wordpress"></i>
        </div>
        <div className="bubble-text">
            {thisLetter.S2.map((p, index) => (<p className="p" key={'P2'+index}>{p}</p>))}
        </div>
        <a href="#SL3" className="scroll">SOME MORE &#8605;</a>
      </div>
      <div id="SL3" className="letter-section s3">
        <div className="bubble-text">
          {thisLetter.S3.map((p, index) => (<p className="p" key={'P3'+index}>{p}</p>))}
        </div>
        <div id="contact">
          <a href="mailto:marie.buerki@gmail.com" target="_blank" rel="noreferrer"><i className="fas fa-at"></i></a>
          <a href="http://linkedin.com/in/marie-bu" target="blank" rel="noreferrer"><i className="fab fa-linkedin"></i></a>
        </div>
      </div>
    </div>
  ))}</div>)
}

export default Letter;